import React, { useState } from "react";
import XLSX from "xlsx";

export const ParseExel = () => {

    const[fileName, setFileName] = useState(null);
    const[table, setTable] = useState(null);


    const handfile = async (e) => {
        const file = e.target.files[0];
        setFileName(file.name);
        const data = await file.arrayBuffer();
        const workbook = XLSX.read(data);

        const worksheet = workbook.Sheets[workbook.SheetNames[0]];
        const jsonData = XLSX.utils.sheet_to_json(worksheet);

        var a = [];
        var b = [];
        for (var key in jsonData) {
            for (var key2 in jsonData[key]){
                a.push(jsonData[key]['__EMPTY_1'] + ' ::url:: ' + jsonData[key]['__EMPTY_7']);
            }
        }
        

        var uniq_url = [...new Set(a)];



        var table = [];
        
            for (var i = 2; i < uniq_url.length; i++) {
                uniq_url[i].toString();
                var left1=uniq_url[i].search('product');
                var new_b = uniq_url[i].slice(left1);

                var req = new XMLHttpRequest();  
                req.open('GET', 'https://www.ozon.ru/api/entrypoint-api.bx/page/json/v2?url=%2F' + new_b + '%2F%3Flayout_container%3DpdpPage2column%26layout_page_index%3D2%26sh%3Da3XYUaTm4Q', false);   
                req.send(null);  
                if(req.status == 200) {
                    var text = req.responseText;
                }
                var left='credentials';
                var right ='logoImageUrl';
                var new_str = text.substring(text.search(left), text.search(right));
                var end = (((new_str.replace('credentials', '')).replaceAll('\\"', '')).replace(':[', '')).replace('],', '');
        
                var left2=uniq_url[i].search(' ::url:: ');
                var array_links = (uniq_url[i].slice(left2)).replace(' ::url:: ', '');
                var array_names = uniq_url[i].slice(0, left2);
                // var table = <tr><td>array_names</td><td>array_links</td><td>end</td></tr>;
                table.push(<tr><td>{array_names}</td><td><a href={array_links}>{array_links}</a></td><td>{end}</td></tr>);
                console.log(table);
            }
            // console.log(table);
        setTable(table);


    }
    return <div>
        <h1>ParseExel</h1>
        {fileName && (
            <p>
                : <span>{fileName}</span>
            </p>
        )}
        <table>
            <thead>
                <tr>
                    <th>Name</th>  
                    <th>URL</th>    
                    <th>Company</th>    
                </tr>
            </thead>
            <tbody>
                {table}
            </tbody>
        </table>
        <input type="file" onChange={(e) => handfile(e)} />
    </div>
}